@import url('https://fonts.googleapis.com/css?family=Work+Sans:400,700&display=swap');

.thumbnailContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	flex-direction: row;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}

.img {
	width: 100x;
	height: 100px;
	border-radius: 100px;
}

.whatWeDoContainer {
	color: white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	padding-top: 50px;
	padding-bottom: 50px;
	font-family: 'Work Sans', sans-serif;
}

.workProfile {
	text-align: center;
	color: #ffffffbf;
	margin: 0;
	margin-top: 20px;
	margin-right: 40px;
	margin-bottom: 20px;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	width: 135px;
}

.heading {
	margin: 0;
	padding: 0;
}

.des {
	margin: 0;
	padding: 0;
	margin-top: 10px;
	margin-bottom: 40px;
	color: #ffffffbf;
}

.quotesContainer {
	text-align: left;
}

@media only screen and (max-width: 728px) {
	.workProfile {
		margin: 0;
		margin: 20px;
	}

	.quotesContainer {
		text-align: center;
	}

	.des {
		width: 80%;
		margin: auto;
		margin-top: 20px;
		margin-bottom: 20px;
		transform: scale(0.8);
		-webkit-transform: scale(0.8);
		-moz-transform: scale(0.8);
		-ms-transform: scale(0.8);
		-o-transform: scale(0.8);
	}

	.thumbnailContainer {
		-ms-flex-pack: space-evenly;
		justify-content: space-evenly;
	}
}

@media only screen and (max-width: 500px) {
	.img {
		-webkit-transform: scale (0.9);
		    -ms-transform: scale (0.9);
		        transform: scale (0.9);
	}
	.thumbnailContainer {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: row;
		flex-direction: row;
		-ms-flex-pack: space-evenly;
		justify-content: space-evenly;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
	}
}

@media only screen and (max-width: 365px) {
	.workProfile {
		margin: 0px;
	}

	.quotesContainer {
		transform: scale (0.9);
		-webkit-transform: scale (0.9);
		-moz-transform: scale (0.9);
		-ms-transform: scale (0.9);
		-o-transform: scale (0.9);
	}

	.img {
		width: 80px;
		height: 80px;
	}
	.des {
		margin-bottom: 50px;
	}
}



