.hrLogo {
	width: 95px;
	float: left;
	padding-bottom: 10px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.emailIcon{
	width: 20px;
	margin-right: 8px;
}


.contact{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
}
.email {
	margin: 0;
	padding: 0;
	color: #154985;
	font-weight: 600;
}

.flexContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	flex-direction: row;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.flexContainer2 {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.logoDiv {
	border-bottom: 3px solid #dddddd;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding-bottom: 10px;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack: center;
	    align-content: center;
}
.companyName {
	font-weight: 900;
	font-size: 3em;
	color: #154985;
	padding: 0;
	margin: 0;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.slogan {
	font-size: 1.4em;
	padding: 0;
	margin: 10px 0px 0px 0px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.superFlexContainer {
	display: -ms-flexbox;
	display: flex;
	overflow-x: hidden;
	-ms-flex-direction: column;
	flex-direction: column;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

@media only screen and (max-width: 870px) {
	.flexContainer2 {
		transform: scale(0.8);
		-moz-transform: scale(0.8);
		-ms-transform: scale(0.8);
		-o-transform: scale(0.8);
		-webkit-transform: scale(0.8);
	}

	.triangles {
		-webkit-transform: scale(0.8);
		    -ms-transform: scale(0.8);
		        transform: scale(0.8);
	}
}

@media only screen and (max-width: 842px) {
	.flexContainer {
		-ms-flex-pack: space-evenly;
		justify-content: space-evenly;
		-ms-flex-direction: column;
		flex-direction: column;
		margin-top: 100px;
	}
	.flexContainer2 {
		width: 500px;
	}

	.triangles {
		margin-top: -150px;
	}
}

@media only screen and (max-width: 836px) {
	.flexContainer2 {
		-webkit-transform: scale(0.7);
		    -ms-transform: scale(0.7);
		        transform: scale(0.7);
	}
	.triangles {
		-webkit-transform: scale(0.7);
		    -ms-transform: scale(0.7);
		        transform: scale(0.7);
		margin-top: -170px;
	}
}

@media only screen and (max-width: 640px) {
	.hrLogo {
		float: none;
		margin: auto;
		display: -ms-flexbox;
	}

	.logoDiv {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.contact {
		width: 0px;
	}

	.mobile {
		font-size: 0px;
	}

	.email {
		font-size: 0px;
	}

	.emailIcon{
		width: 0px;
	}
}

@media only screen and (max-width: 376px) {
	.flexContainer2 {
		transform: scale(0.8);
		-webkit-transform: scale(0.8);
		-moz-transform: scale(0.8);
		-ms-transform: scale(0.8);
		-o-transform: scale(0.8);
		text-align: center;
	}
	.triangles {
		transform: scale(0.6);
		-webkit-transform: scale(0.6);
		-moz-transform: scale(0.6);
		-ms-transform: scale(0.6);
		-o-transform: scale(0.6);
		margin-top: -100px;
	}
}
@media only screen and (max-width: 320px) {
	.triangles {
		transform: scale(0.5);
		-webkit-transform: scale(0.5);
		-moz-transform: scale(0.5);
		-ms-transform: scale(0.5);
		-o-transform: scale(0.5);
	}
}



